.form {
  margin-top: 20px;
  display: flex;
  flex-direction: column;
  align-items: center;
  .form-width {
    width: 80%;
    align-self: flex-start;
  }
  .form-width-color {
    width: 25%;
    display: flex;
    flex-wrap: nowrap;
    justify-content: space-between;
    align-self: flex-start;
    height: fit-content;
    border-bottom: none !important;
  }
  .color {
    width: 90%;
    border: none !important;
  }

  .form-warning-msg {
    color: red;
    font-weight: 500;
    line-height: 1.2;
    width: fit-content;
  }
  input {
    margin: 10px;
    padding: 6px 10px 6px 10px;
    border-radius: 10px;
    font-weight: 500;
    background-color: rgb(240, 235, 235);
    font-family: montserrat;
    font-size: rem(14px);

    &:focus {
      outline: none;
    }
  }
}
